import type { Conversation } from "./types"
import { storage } from "./storage"
import { db } from "./supabase/db"
import { v4 as uuidv4 } from "uuid"

const NOTES_EMAIL_PREFIX = "notes@"
const NOTES_NAME = "Notes"

// Store notes conversation IDs in memory (businessId -> conversationId)
// This allows us to use proper UUIDs while still identifying notes conversations
const notesConversationIdMap = new Map<string, string>()

/**
 * Get the notes conversation ID for a business
 * Uses a cached UUID or generates a new one
 */
export function getNotesConversationId(businessId: string): string {
  if (!notesConversationIdMap.has(businessId)) {
    notesConversationIdMap.set(businessId, uuidv4())
  }
  return notesConversationIdMap.get(businessId)!
}

/**
 * Check if a conversation is a notes conversation
 */
export function isNotesConversation(conversation: Conversation | string): boolean {
  if (typeof conversation === "string") {
    // For string IDs, we can't determine from just the ID
    // This will be checked when we have the full conversation object
    // Return false for now - the check will happen when conversation is loaded
    return false
  }
  // Check by email pattern (most reliable)
  return conversation.customerEmail?.startsWith(NOTES_EMAIL_PREFIX) || false
}

/**
 * Check if a conversation ID might be a notes conversation
 * This is a helper for when we only have the ID string
 */
export async function isNotesConversationById(conversationId: string, businessId: string): Promise<boolean> {
  try {
    const conversation = await storage.getConversationById(conversationId)
    if (conversation) {
      return isNotesConversation(conversation)
    }
    // If conversation doesn't exist, check if the email pattern matches
    const notesEmail = `${NOTES_EMAIL_PREFIX}${businessId}.local`
    // We can't check without the conversation, so return false
    return false
  } catch {
    return false
  }
}

/**
 * Get or create the notes conversation for a business
 */
export async function getOrCreateNotesConversation(businessId: string): Promise<Conversation> {
  const notesEmail = `${NOTES_EMAIL_PREFIX}${businessId}.local`
  
  // First, try to find existing notes conversation by email pattern
  try {
    const conversations = await storage.getConversationsByBusinessId(businessId)
    const existingNotes = conversations.find((conv) => conv.customerEmail === notesEmail)
    if (existingNotes) {
      // Cache the ID for future lookups
      notesConversationIdMap.set(businessId, existingNotes.id)
      return existingNotes
    }
  } catch (error) {
    console.error("Error fetching conversations to find notes:", error)
  }

  // If not found, create a new notes conversation with a proper UUID
  const notesId = getNotesConversationId(businessId)
  const notesConversation: Conversation = {
    id: notesId,
    businessId,
    customerEmail: notesEmail,
    customerName: NOTES_NAME,
    createdAt: new Date().toISOString(),
    lastMessageAt: new Date().toISOString(),
    messages: [],
  }

  try {
    await storage.createConversation(notesConversation)
    const created = await storage.getConversationById(notesId)
    if (created) {
      return created
    }
  } catch (error: any) {
    // Log detailed error information
    console.error("Error creating notes conversation:", {
      error,
      message: error?.message,
      code: error?.code,
      details: error?.details,
      hint: error?.hint,
      notesId,
      notesEmail,
      businessId,
    })
    
    // Check if it's a unique constraint violation (conversation already exists)
    // In that case, try to find it by email pattern
    if (error?.code === "23505" || error?.message?.includes("duplicate") || error?.message?.includes("unique")) {
      try {
        const conversations = await storage.getConversationsByBusinessId(businessId)
        const existingNotes = conversations.find((conv) => conv.customerEmail === notesEmail)
        if (existingNotes) {
          notesConversationIdMap.set(businessId, existingNotes.id)
          return existingNotes
        }
      } catch (fetchError) {
        console.error("Error fetching existing notes conversation:", fetchError)
      }
    }
    
    // If creation fails, return the conversation object anyway
    // It will be created on first message send
  }

  return notesConversation
}

